import { Router } from 'express'
import { Op, QueryTypes } from 'sequelize'
import sequelize from '../models/sequelize'
import toRes from '../lib/toRes'
import QiuzhijianliModel from '../models/QiuzhijianliModel'
import util from '../lib/util'
import jwt from 'jsonwebtoken'

export default ({ config, db }) => {
	let api = Router()

	// 分页接口（后端）
	api.get('/page', async (req, res) => {

		try {
			let page = parseInt(req.query.page) || 1
			let limit = parseInt(req.query.limit) || 10
			let sort = req.query.sort || 'id'
			let order = req.query.order || 'asc'

			let where = {}
			let xuehao = req.query.xuehao
			if (xuehao) {

				if (xuehao.indexOf('%') != -1) {
					where.xuehao = {
						[Op.like]: xuehao
					}
				} else {
					where.xuehao = {
						[Op.eq]: xuehao
					}
				}
			}
			let xueshengxingming = req.query.xueshengxingming
			if (xueshengxingming) {

				if (xueshengxingming.indexOf('%') != -1) {
					where.xueshengxingming = {
						[Op.like]: xueshengxingming
					}
				} else {
					where.xueshengxingming = {
						[Op.eq]: xueshengxingming
					}
				}
			}
			let touxiang = req.query.touxiang
			if (touxiang) {

				if (touxiang.indexOf('%') != -1) {
					where.touxiang = {
						[Op.like]: touxiang
					}
				} else {
					where.touxiang = {
						[Op.eq]: touxiang
					}
				}
			}
			let xingbie = req.query.xingbie
			if (xingbie) {

				if (xingbie.indexOf('%') != -1) {
					where.xingbie = {
						[Op.like]: xingbie
					}
				} else {
					where.xingbie = {
						[Op.eq]: xingbie
					}
				}
			}
			let nianling = req.query.nianling
			if (nianling) {

				if (nianling.indexOf('%') != -1) {
					where.nianling = {
						[Op.like]: nianling
					}
				} else {
					where.nianling = {
						[Op.eq]: nianling
					}
				}
			}
			let zhuanye = req.query.zhuanye
			if (zhuanye) {

				if (zhuanye.indexOf('%') != -1) {
					where.zhuanye = {
						[Op.like]: zhuanye
					}
				} else {
					where.zhuanye = {
						[Op.eq]: zhuanye
					}
				}
			}
			let xueli = req.query.xueli
			if (xueli) {

				if (xueli.indexOf('%') != -1) {
					where.xueli = {
						[Op.like]: xueli
					}
				} else {
					where.xueli = {
						[Op.eq]: xueli
					}
				}
			}
			let biyeyuanxiao = req.query.biyeyuanxiao
			if (biyeyuanxiao) {

				if (biyeyuanxiao.indexOf('%') != -1) {
					where.biyeyuanxiao = {
						[Op.like]: biyeyuanxiao
					}
				} else {
					where.biyeyuanxiao = {
						[Op.eq]: biyeyuanxiao
					}
				}
			}
			let waiyushuiping = req.query.waiyushuiping
			if (waiyushuiping) {

				if (waiyushuiping.indexOf('%') != -1) {
					where.waiyushuiping = {
						[Op.like]: waiyushuiping
					}
				} else {
					where.waiyushuiping = {
						[Op.eq]: waiyushuiping
					}
				}
			}
			let qiuzhiyixiang = req.query.qiuzhiyixiang
			if (qiuzhiyixiang) {

				if (qiuzhiyixiang.indexOf('%') != -1) {
					where.qiuzhiyixiang = {
						[Op.like]: qiuzhiyixiang
					}
				} else {
					where.qiuzhiyixiang = {
						[Op.eq]: qiuzhiyixiang
					}
				}
			}
			let qiwangxinzi = req.query.qiwangxinzi
			if (qiwangxinzi) {

				if (qiwangxinzi.indexOf('%') != -1) {
					where.qiwangxinzi = {
						[Op.like]: qiwangxinzi
					}
				} else {
					where.qiwangxinzi = {
						[Op.eq]: qiwangxinzi
					}
				}
			}
			let shehuishijian = req.query.shehuishijian
			if (shehuishijian) {

				if (shehuishijian.indexOf('%') != -1) {
					where.shehuishijian = {
						[Op.like]: shehuishijian
					}
				} else {
					where.shehuishijian = {
						[Op.eq]: shehuishijian
					}
				}
			}
			let gerenjianjie = req.query.gerenjianjie
			if (gerenjianjie) {

				if (gerenjianjie.indexOf('%') != -1) {
					where.gerenjianjie = {
						[Op.like]: gerenjianjie
					}
				} else {
					where.gerenjianjie = {
						[Op.eq]: gerenjianjie
					}
				}
			}
			let userid = req.query.userid
			if (userid) {

				if (userid.indexOf('%') != -1) {
					where.userid = {
						[Op.like]: userid
					}
				} else {
					where.userid = {
						[Op.eq]: userid
					}
				}
			}
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'xuesheng') {
				where.xuehao = {
					[Op.eq]: req.session.userinfo === undefined ? jwt.decode(req.headers.token).username : req.session.userinfo.xuehao
				}
				if (where['userid'] != undefined) {
					delete where.userid
				}
			}

			let result = await QiuzhijianliModel.findAndCountAll({
				order: [[sort, order]],
				where,
				offset: (page - 1) * limit,
				limit
			})
			
			result.currPage = page
			result.pageSize = limit

			toRes.page(res, 0, result)
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 分页接口（前端）
	api.get('/list', async (req, res) => {

		try {

			let page = parseInt(req.query.page) || 1
			let limit = parseInt(req.query.limit) || 10
			let sort = req.query.sort || 'id'
			let order = req.query.order || 'asc'

			let where = {}
			let xueshengxingming = req.query.xueshengxingming
			if (xueshengxingming) {

				if (xueshengxingming.indexOf('%') != -1) {
					where.xueshengxingming = {
						[Op.like]: xueshengxingming
					}
				} else {
					where.xueshengxingming = {
						[Op.eq]: xueshengxingming
					}
				}
			}
			let zhuanye = req.query.zhuanye
			if (zhuanye) {

				if (zhuanye.indexOf('%') != -1) {
					where.zhuanye = {
						[Op.like]: zhuanye
					}
				} else {
					where.zhuanye = {
						[Op.eq]: zhuanye
					}
				}
			}
			let qiuzhiyixiang = req.query.qiuzhiyixiang
			if (qiuzhiyixiang) {

				if (qiuzhiyixiang.indexOf('%') != -1) {
					where.qiuzhiyixiang = {
						[Op.like]: qiuzhiyixiang
					}
				} else {
					where.qiuzhiyixiang = {
						[Op.eq]: qiuzhiyixiang
					}
				}
			}
			// let tableName = req.session.userinfo.tableName
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'xuesheng') {
				where.xuehao = {
					[Op.eq]: req.session.userinfo === undefined ? jwt.decode(req.headers.token).username : req.session.userinfo.xuehao
				}
			}


			let result = await QiuzhijianliModel.findAndCountAll({
				order: [[sort, order]],
				where,
				offset: (page - 1) * limit,
				limit
			})
			
			result.currPage = page
			result.pageSize = limit

			toRes.page(res, 0, result)
		} catch(err) {
			
			toRes.session(res, 401, '您的权限不够！', '', 200)
		}
	})


	// 保存接口（后端）
	api.post('/save', async (req, res) => {

		try {

			Object.keys(req.body).forEach(item=>{
				if(req.body[item] == '')  delete req.body[item]
			})

			if (!req.body.userid) {
				req.body.userid = req.session.userinfo === undefined ? jwt.decode(req.headers.token).id : req.session.userinfo.id
			}
			const userinfo = await QiuzhijianliModel.create(req.body)

			if (userinfo === null) {
				toRes.session(res, -1, '添加失败！')
			} else {
				toRes.session(res, 0, '添加成功！')
			}
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 保存接口（前端）
	api.post('/add', async (req, res) => {

		try {

			Object.keys(req.body).forEach(item=>{
				if(req.body[item] == '')  delete req.body[item]
			})

			if (jwt.decode(req.headers.token) == null) {
				toRes.session(res, 401, '请登录后再操作', '', 401)
			}

			req.body.userid = req.session.userinfo === undefined ? jwt.decode(req.headers.token).id : req.session.userinfo.id

			const userinfo = await QiuzhijianliModel.create(req.body)

			if (userinfo === null) {
				toRes.session(res, -1, '添加失败！')
			} else {
				toRes.session(res, 0, '添加成功！')
			}
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 更新接口
	api.post('/update', async (req, res) => {

		try {
			await QiuzhijianliModel.update(req.body, {
				where: {
				  id: req.body.id || 0
				}
			})

			toRes.session(res, 0, '编辑成功！')
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 删除接口
	api.post('/delete', async (req, res) => {

		try {

			await QiuzhijianliModel.destroy({
				where: {
				  id: {
					[Op.in]: req.body
				  }
				}
			})
			toRes.session(res, 0, '删除成功！')
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 详情接口（后端）
	api.all('/info/:id', async (req, res) => {

		try {
			toRes.record(res, 0, await QiuzhijianliModel.findOne({ where: { id: req.params.id } }))
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

    // 详情接口（前端）
	api.all('/detail/:id', async (req, res) => {

		try {
			toRes.record(res, 0, await QiuzhijianliModel.findOne({ where: { id: req.params.id } }))
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 获取需要提醒的记录数接口
	api.get('/remind/:columnName/:type', async (req, res) => {

		try {

			let sql = 'SELECT 0 AS count'
			
			if (req.params.type == 1) {
				if (req.query.remindstart) sql = "SELECT COUNT(*) AS count FROM qiuzhijianli WHERE " + req.params.columnName + " >= '" + req.query.remindstart + "'"
				if (req.query.remindend) sql = "SELECT COUNT(*) AS count FROM qiuzhijianli WHERE " + req.params.columnName + " <= '" + req.query.remindend + "'"

				if (req.query.remindstart && req.query.remindend) {
					sql = "SELECT COUNT(*) AS count FROM qiuzhijianli WHERE " + req.params.columnName + " >= '" + req.query.remindstart + "' AND " + req.params.columnName + " <= '" + req.query.remindend + "'"
				}
			}

			if (req.params.type == 2) {
				if (req.query.remindstart) {
					let remindStart = util.getDateTimeFormat(0 + Number(req.query.remindstart), "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM qiuzhijianli WHERE " + req.params.columnName + " >= '" + remindStart + "'"
				}
				if (req.query.remindend) {
					let remindEnd = util.getDateTimeFormat(req.query.remindend, "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM qiuzhijianli WHERE " + req.params.columnName + " <= '" + remindEnd + "'"
				}

				if (req.query.remindstart && req.query.remindend) {
					let remindStart = util.getDateTimeFormat(0 + Number(req.query.remindstart), "yyyy-MM-dd")
					let remindEnd = util.getDateTimeFormat(req.query.remindend, "yyyy-MM-dd")
					sql = "SELECT COUNT(*) AS count FROM qiuzhijianli WHERE " + req.params.columnName + " >= '" + remindStart + "' AND " + req.params.columnName + " <= '" + remindEnd + "'"
				}
			}

			const results = await sequelize.query(sql, {
				plain: true,
				raw: true,
				type: QueryTypes.SELECT
			})

			toRes.count(res, 0, results.count)
		} catch(err) {	
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 分组统计接口
	api.get('/group/:columnName', async (req, res) => {

		try {

			let sql = ""
			let columnName = req.params.columnName
			// let tableName = "qiuzhijianli"
			let where = " WHERE 1 = 1 "
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'xuesheng') {
				where += " AND xuehao = '" + jwt.decode(req.headers.token).username + "' ";
			}
			sql = "SELECT COUNT(*) AS total, " + columnName + " FROM qiuzhijianli " + where + " GROUP BY " + columnName + " LIMIT 10" 
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 统计指定字段
	api.get('/value/:xColumnName/:yColumnName', async (req, res) => {

		try {

			let sql = ""
			let xColumnName = req.params.xColumnName
			let yColumnName = req.params.yColumnName
			let where = " WHERE 1 = 1 "
			let tableName = req.session.userinfo === undefined ? jwt.decode(req.headers.token).tableName : req.session.userinfo.tableName
			if(tableName == 'xuesheng') {
				where += " AND xuehao = '" + jwt.decode(req.headers.token).username + "' ";
			}
			sql = "SELECT " + xColumnName + ", SUM(" + yColumnName + ") AS total FROM qiuzhijianli " + where + " GROUP BY " + xColumnName + " DESC LIMIT 10"
			
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {

			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	// 按日期统计
	api.get('/value/:xColumnName/:yColumnName/:timeStatType', async (req, res) => {

		try {
			
			let sql = ""
			let xColumnName = req.params.xColumnName
			let yColumnName = req.params.yColumnName
			let timeStatType = req.params.timeStatType
			let tableName = "qiuzhijianli"
			let where = " WHERE 1 = 1 "
			if (jwt.decode(req.headers.token).role != '管理员') {
				where += " AND xuehao = '" + jwt.decode(req.headers.token).username + "' ";
			}

            if (config.dbConnection.dbtype.toLowerCase() == "mysql") {
                if (timeStatType == "日")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y-%m-%d') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y-%m-%d') LIMIT 10";
                if (timeStatType == "月")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y-%m') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y-%m')  LIMIT 10";
                if (timeStatType == "年")
                    sql = "SELECT DATE_FORMAT(" + xColumnName + ", '%Y') " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(" + xColumnName + ", '%Y')  LIMIT 10";
            } else {
                if (timeStatType == "日")
                    sql = "SELECT DATE_FORMAT(VARCHAR(10)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(10)," + xColumnName + ", 120)  LIMIT 10";
                if (timeStatType == "月")
                    sql = "SELECT DATE_FORMAT(VARCHAR(7)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(7)," + xColumnName + ", 120)  LIMIT 10";
                if (timeStatType == "年")
                    sql = "SELECT DATE_FORMAT(VARCHAR(4)," + xColumnName + ", 120) " + xColumnName + ", sum(" + yColumnName + ") total FROM " + tableName + where + " GROUP BY DATE_FORMAT(VARCHAR(4)," + xColumnName + ", 120)  LIMIT 10";
            }
			toRes.record(res, 0, await sequelize.query(sql, {
				plain: false,
				raw: true,
				type: QueryTypes.SELECT
			}))
		} catch(err) {
			toRes.session(res, 500, '服务器错误！', '', 500)
		}
	})

	return api
}
